import { Component } from '@angular/core';
import { FormGroup, FormControl, Validator, FormBuilder, Validators } from '@angular/forms'
import { StrayModuleService } from 'src/app/shared/services/stray.module.service';
import { NotificationAlertService,AESEncryptDecryptService } from 'bd-common-lib';
import { AppConstants } from 'src/app/app.constants';
import { CsvService } from 'src/app/shared/services/csv.download.service';
import { Title } from '@angular/platform-browser';

@Component({
  selector: 'app-alternate-customer-master',
  templateUrl: './alternate-customer-master.component.html',
  styleUrls: ['./alternate-customer-master.component.css'],
})
export class AlternateCustomerMasterComponent {

  customerForm!: FormGroup;
  customerData: any[] = [];
  fsmData: any[] = [];
  templateError = '';
  constructor(
    private fb: FormBuilder,
    private strayModuleService: StrayModuleService,
    private csvService: CsvService,
    public alert: NotificationAlertService,
    private crypto:AESEncryptDecryptService,
    private title:Title) { }

  ngOnInit(): void {
    this.title.setTitle("Alternate Customer Master")
    this.createForm();
  }

  createForm() {
    this.customerForm = this.fb.group({
      pccompgrp: ['', [Validators.maxLength(6), Validators.required]],
      pcregion: ['', [Validators.maxLength(6), Validators.required]],
      pcbranch: ['', [Validators.maxLength(3)]],
      pcarea: ['', [Validators.maxLength(3), Validators.required]],
      pccustcode: ['', [Validators.maxLength(6), Validators.required]],
      pcprodcode: ['', [Validators.maxLength(1), Validators.required]],
      pcmode: ['', Validators.maxLength(1)],
      pcpacktype: ['', Validators.maxLength(1)],
    });
    this.fsmData=[];
  }

  downloadCSV() {
    const payload = {
      "mcemplcode": "",
      "pccompgrp": "",
      "pcregion": "",
      "pcbranch": "",
      "pcarea": "",
      "pccustcode": "",
      "pcprodcode": "",
      "pcmode": "",
      "pcpacktype": "",
      "mcsubmit": "DOWNLOAD_CSV"
    }
    this.strayModuleService.postDetails(payload, AppConstants.ALTERNATE_CUSTOMER_ENTRY).subscribe((res) => {
      if (res.result) {
        this.alert.showSuccess('CSV Downloaded successfully', 'toast-bottom-right');
        this.csvService.exportToCsv('ALTERNATE_CUSTOMER_MASTER_ENTRY.csv', res.result.p_cursor)
      }
    }, (err) => {
      this.alert.showError('Error occured try again !!', 'toast-bottom-right');
    })
  }

  fetchDetails() {
    if (this.customerForm.value.pccompgrp) {
      const payload = {
        "mcemplcode": "",
        "pccompgrp": this.customerForm.value.pccompgrp,
        "pcregion": "",
        "pcbranch": "",
        "pcarea": "",
        "pccustcode": "",
        "pcprodcode": "",
        "pcmode": "",
        "pcpacktype": "",
        "mcsubmit": "DOWNLOAD_CSV"
      }
      this.templateError='';
      this.strayModuleService.postDetails(payload, AppConstants.ALTERNATE_CUSTOMER_ENTRY).subscribe((res) => {
        if (res.result) {
          this.fsmData = res.result.p_cursor;
        }
      }, (err) => {
        this.alert.showError('Error occured try again !!', 'toast-bottom-right');
      })
    }
  }

  saveDetails() {
    const requestBody = {
      "mcemplcode": this.crypto.decryptionAES(sessionStorage.getItem('empCode')),
      "pccompgrp": this.customerForm.value.pccompgrp,
      "pcregion": this.customerForm.value.pcregion,
      "pcbranch": this.customerForm.value.pcbranch,
      "pcarea": this.customerForm.value.pcarea,
      "pccustcode": this.customerForm.value.pccustcode,
      "pcprodcode": this.customerForm.value.pcprodcode,
      "pcmode": this.customerForm.value.pcmode,
      "pcpacktype": this.customerForm.value.pcpacktype,
      "mcsubmit": "SAVE"
    }
    this.strayModuleService.postDetails(requestBody, AppConstants.ALTERNATE_CUSTOMER_ENTRY).subscribe((res) => {
      if (res.result.cvalidflag=="Y") {
        this.alert.showSuccess('Data Saved Succesfully', 'toast-bottom-right');
        this.createForm();
      }
      else
      {
        this.templateError=res.result.cerrmsg;
      }
    }, (err) => {
      this.alert.showError('Error occured try again !!', 'toast-bottom-right');
    })
  }

  removeDetails() {
    if (this.customerForm.valid) {
      let requestBody = this.customerForm.getRawValue();
      requestBody.mcsubmit = 'DELETE';
      this.strayModuleService.postDetails(requestBody, AppConstants.ALTERNATE_CUSTOMER_ENTRY).subscribe((res) => {
        if (res.result) {
          this.alert.showSuccess('Data Deleted Succesfully', 'toast-bottom-right');
          this.createForm();
        }
      }, (err) => {
        this.alert.showError('Error occured try again !!', 'toast-bottom-right');
      })
    }
  }

  patchForm(cus: any) {
    this.customerForm.patchValue(cus);
    this.customerForm.patchValue({
      pccompgrp: cus.Group_Code,
      pcregion: cus.Region,
      pcbranch: cus.Branch,
      pcarea: cus.Area,
      pccustcode: cus.Customer_Code,
      pcprodcode:cus.Product_Code,
      pcmode:cus.Mode,
      pcpacktype:cus.Pack_Type
     });
    this.customerForm.patchValue(cus);
    this.customerData = [];
  }
}
