import { Component } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validator, Validators } from '@angular/forms';
import { StrayModuleService } from 'src/app/shared/services/stray.module.service';
import { NotificationAlertService } from 'bd-common-lib';
import { AppConstants } from 'src/app/app.constants';
import { Title } from '@angular/platform-browser';
@Component({
  selector: 'app-customer-group-enrty',
  templateUrl: './customer-group-enrty.component.html',
  styleUrls: ['./customer-group-enrty.component.css']
})
export class CustomerGroupEnrtyComponent {

  notifcationForm!: FormGroup;

  constructor(
    private fb: FormBuilder,
    private strayModuleService: StrayModuleService,
    public alert: NotificationAlertService,
    private title:Title) { }

  ngOnInit(): void {
    this.title.setTitle('Notification');
    this.createForm();
  }

  createForm() {
    this.notifcationForm = this.fb.group({
      mcemplcode: ['00007'],
      mcgroupname: ['', [Validators.required]],
    });
  }

  onSubmit() {
    let requestBody = this.notifcationForm.getRawValue();
    this.strayModuleService.postDetails(requestBody, AppConstants.NOTIFICATION_ENTRY).subscribe((res: any) => {
      if (res.result) {
        this.alert.showSuccess('Data Saved Successfully', 'toast-bottom-right');
      }
    }, (err) => {
      this.alert.showError('Error occured try again!!', 'toast-bottom-right');
    })
  }

}
