import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { AppConstants } from 'src/app/app.constants';

@Injectable({
  providedIn: 'root',
})
export class NotificationModuleService {
  constructor(private httpClient: HttpClient) { }

  postDetails(payload: any, endPoint: string): Observable<any> {
    return this.httpClient.post(endPoint, payload);
  } 

  public GetNotificationDetails(body: any): Observable<any> {
    return this.httpClient.post<any>(AppConstants.NOTIFICATION_TYPE,{params:body});
    } 
}
