import { Component } from '@angular/core';
import {FormBuilder, FormControl,FormGroup,Validator, Validators} from '@angular/forms';
import { NotificationAlertService } from 'bd-common-lib';
import { AppConstants } from 'src/app/app.constants';
import { CsvService } from 'src/app/shared/services/csv.download.service';
import { StrayModuleService } from 'src/app/shared/services/stray.module.service';
@Component({
  selector: 'app-notificationentryform',
  templateUrl: './notificationentryform.component.html',
  styleUrls: ['./notificationentryform.component.css']
})
export class NotificationentryformComponent {

  customerForm!: FormGroup;
  customerData: any[] = [];
  fsmData: any[] = [];
  templateError = ''; 

  notificationentryform = new FormGroup({
    notificationtype:new FormControl(''),
    customergroup:new FormControl(''),
    area:new FormControl(''),
    custcode:new FormControl(''),    
  })

  constructor(
    private fb: FormBuilder,
    private strayModuleService: StrayModuleService,
    private csvService: CsvService,
    public alert: NotificationAlertService) { }

  ngOnInit(): void {
    this.createForm();
  }

  createForm() {
    this.notificationentryform = this.fb.group({
      notificationtype: ['', [Validators.required]],
      customergroup: ['', [Validators.maxLength(6), Validators.required]],
      area: ['', [Validators.maxLength(3)]],
      custcode: ['', [Validators.maxLength(3), Validators.required]],
    });
    this.fsmData=[];
  }

  downloadCSV() {
    const payload = {
      "mcemplcode": "",
      "pccompgrp": "",
      "pcregion": "",
      "pcbranch": "",
      "pcarea": "",
      "pccustcode": "",
      "pcprodcode": "",
      "pcmode": "",
      "pcpacktype": "",
      "mcsubmit": "DOWNLOAD_CSV"
    }
    this.strayModuleService.postDetails(payload, AppConstants.ALTERNATE_CUSTOMER_ENTRY).subscribe((res) => {
      if (res.result) {
        this.alert.showSuccess('CSV Downloaded successfully', 'toast-bottom-right');
        this.csvService.exportToCsv('ALTERNATE_CUSTOMER_MASTER_ENTRY.csv', res.result.p_cursor)
      }
    }, (err) => {
      this.alert.showError('Error occured try again !!', 'toast-bottom-right');
    })
  }

  fetchDetails() {
    if (this.customerForm.value.pccompgrp) {
      const payload = {
        "mcemplcode": "",
        "pccompgrp": this.customerForm.value.pccompgrp,
        "pcregion": "",
        "pcbranch": "",
        "pcarea": "",
        "pccustcode": "",
        "pcprodcode": "",
        "pcmode": "",
        "pcpacktype": "",
        "mcsubmit": "DOWNLOAD_CSV"
      }
      this.templateError='';
      this.strayModuleService.postDetails(payload, AppConstants.ALTERNATE_CUSTOMER_ENTRY).subscribe((res) => {
        if (res.result) {
          this.fsmData = res.result.p_cursor;
        }       
      }, (err) => {
        this.alert.showError('Error occured try again !!', 'toast-bottom-right');
      })
    }
  }

  saveDetails() {
    let requestBody = this.customerForm.getRawValue();
    requestBody.mcsubmit = 'SAVE';
    this.strayModuleService.postDetails(requestBody, AppConstants.ALTERNATE_CUSTOMER_ENTRY).subscribe((res) => {
      if (res.result.cvalidflag=="Y") {
        this.alert.showSuccess('Data Saved Succesfully', 'toast-bottom-right');
        this.createForm();
      }
      else
      {
        this.templateError=res.result.cerrmsg;
      }
    }, (err) => {
      this.alert.showError('Error occured try again !!', 'toast-bottom-right');
    })
  }



}
