import { Component, OnInit, ViewChild } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { AESEncryptDecryptService } from 'bd-common-lib';
import { AppConstants } from 'src/app/app.constants';

@Component({
  selector: 'app-stray-module',
  templateUrl: './stray-module.component.html',
  styleUrls: ['./stray-module.component.css']
})

export class StrayModuleComponent implements OnInit {
  activeComponent: any;
  showEntryFormAsPerRole:boolean=true;

  constructor(    
    private title:Title,
    private crypto:AESEncryptDecryptService
    ) { }

  ngOnInit(): void {
    this.title.setTitle("Stray Module")
    //this.assignComponent('StrayEntryComponent', '');

    if(this.crypto.decryptionAES(AppConstants.EmpCLevel) === "0")
    {
      this.showEntryFormAsPerRole = false;
    }
    else
    {
      this.showEntryFormAsPerRole = true;
    }
  }

  assignComponent(component:string, strayDetail:any){
      this.activeComponent=component;
  }

  logout()
  {
    window.opener.location.href = 'http://localhost:4200/dashboard?mode=logout'; 
    //window.close();
  }
  
}
