import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { HomeComponent } from './components/home/home.component';
import { AlternateCustomerMasterComponent } from './components/alternate-customer-master/alternate-customer-master.component';
import { AwbListingusComponent } from './components/awb-listingus/awb-listingus.component';
import { CAPXModuleComponent } from './components/capx-module/capx-module.component';
import { MISSchedularComponent } from './components/mis-schedular/mis-schedular.component';
import { StrayModuleComponent } from './components/stray-module/stray-module.component';
import { NotificationComponent } from './components/notification/notification.component';
import { QueryComponent } from './components/query/query.component';
import { CustomerMasterComponent } from './components/customer-master/customer-master.component';
import { CustomerGroupEnrtyComponent } from './components/customer-group-enrty/customer-group-enrty.component';
import { StrayUpdateComponent } from './components/stray-update/stray-update.component';
import { UnAuthorizedAccessComponent } from './shared/components/un-authorized-access/un-authorized-access.component';


const routes: Routes = [
  { path: '', redirectTo: '/dashboard', pathMatch: 'full'},
  { path: 'dashboard', component: HomeComponent},
  { path:'Alternate_Customer_master', component:AlternateCustomerMasterComponent},
  { path:'awb_listing', component:AwbListingusComponent},
  { path:'CAPX_Module', component:CAPXModuleComponent},
  { path:'MIS_Schedular', component:MISSchedularComponent},
  { path:'Stray_module', component:StrayModuleComponent},
  { path:'Notification',component:NotificationComponent},
  { path:'customer-master',component:CustomerMasterComponent},
  { path:'customer-group-enrty',component:CustomerGroupEnrtyComponent},
  { path:'un-authorized-access',component: UnAuthorizedAccessComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
