import { HttpParams } from '@angular/common/http';
import { Component } from '@angular/core';
import { FormGroup, FormControl, Validator, FormBuilder, Validators } from '@angular/forms'
import { NotificationAlertService,AESEncryptDecryptService } from 'bd-common-lib';
import { AppConstants } from 'src/app/app.constants';
import { CsvService } from 'src/app/shared/services/csv.download.service';
import { NotificationModuleService } from 'src/app/shared/services/notification.module.service';
import { Title } from '@angular/platform-browser';

@Component({
  selector: 'app-customer-master',
  templateUrl: './customer-master.component.html',
  styleUrls: ['./customer-master.component.css']
})

export class CustomerMasterComponent {
  customerForm!: FormGroup;
  customerData: any[] = [];
  fsmData: any[] = [];
  productList: any[] = [];
  templateError = ''; 
  templateDone='';
  constructor(
    private fb: FormBuilder,
    private notificationModuleService: NotificationModuleService,
    private csvService: CsvService,
    private crypto:AESEncryptDecryptService,
    public alert: NotificationAlertService,
    private title:Title) { }

  ngOnInit(): void {
    this.title.setTitle('Notification');
    this.createForm();
    //this.getNotiDetails();
  }

  createForm() {
    this.customerForm = this.fb.group({
      mctype: ['', [Validators.required]],
      Area: ['', [Validators.required]],
      Custcode: ['', [Validators.required]],
      Mailto: [''],
      Mailcc: [''],
      pcMobileNo: [''],
      mcpart1: [''],
      mcpart2: [''],
    });
    this.fsmData=[];
  }

  downloadCSV() {
    const payload = {
      "mcEmplCode": "",
      "mcarea": "",
      "mccustcode": "",
      "mcnotitype": "",
      "mctoemailid": "",
      "mcccemailid": "",
      "mAdd": "",
      "mCPART1": "",
      "mCPART2": "",
      "mMobileNo": "",
      "mcgroupcode": ""
    }
    
    this.notificationModuleService.postDetails(payload, AppConstants.NOTIFICATION_MASTER_ENTRY).subscribe((res) => {
      if (res.result) {
        this.alert.showSuccess('CSV Downloaded successfully', 'toast-bottom-right');
        this.csvService.exportToCsv('ALTERNATE_CUSTOMER_MASTER_ENTRY.csv', res.result.p_cursor)
      }
    }, (err) => {
      this.alert.showError('Error occured try again !!', 'toast-bottom-right');
    })
  }

  
  patchForm(cus: any) {
    this.customerForm.patchValue(cus);
    this.customerForm.patchValue({ 
      Custcode: cus.CCUSTCODE,
      Mailto: cus.CTOEMAILID,
      Mailcc: cus.CCCEMAILID
     });
    this.customerForm.patchValue(cus);
    this.customerData = [];
  }

  getNotiDetails() {
    {
      let queryParams = new HttpParams();
      queryParams = queryParams.append('pprodvar','test');
      this.notificationModuleService.GetNotificationDetails(queryParams).subscribe((res: any) => {
        if (res.result) {
          this.productList = res.result.p_cursor;
        }
      })      
    }
  }

  fetchDetails() {
    this.templateError='';
    if (this.customerForm.value.Custcode) {
      const payload = {
        "mcEmplCode": "",
        "mcarea": this.customerForm.value.Area,
        "mccustcode": this.customerForm.value.Custcode,
        "mcnotitype": this.customerForm.value.mctype,
        "mctoemailid": "S1",
        "mcccemailid": "",
        "mcAdd": "fetch",
        "mcPART1": "",
        "mcPART2": "",
        "mcMobileNo": "",
        "mcgroupcode": ""
      }
      this.templateError='';
      this.notificationModuleService.postDetails(payload, AppConstants.NOTIFICATION_MASTER_ENTRY).subscribe((res) => {
        if (res.result.mvalidFlag=='Y') {
          this.fsmData = res.result.p_cursor;
        }
        else
        {
          this.templateError=res.result.cerrmsg;
        }       
      }, (err) => {
        this.alert.showError('Error occured try again !!', 'toast-bottom-right');
      })
    }
  }

  saveDetails() {
    this.templateError='';
    this.templateDone='';
    const payload = {
      "mcEmplCode": this.crypto.decryptionAES(sessionStorage.getItem('empCode')),
      "mcarea": this.customerForm.value.Area,
      "mccustcode": this.customerForm.value.Custcode,
      "mcnotitype": this.customerForm.value.mctype,
      "mctoemailid": this.customerForm.value.Mailto,
      "mcccemailid": this.customerForm.value.Mailcc,
      "mcAdd": "SUBMIT",
      "mcPART1": this.customerForm.value.mcpart1,
      "mcPART2": this.customerForm.value.mcpart2,
      "mcMobileNo": this.customerForm.value.pcMobileNo,
      "mcgroupcode": ""
    };
    this.notificationModuleService.postDetails(payload, AppConstants.NOTIFICATION_MASTER_ENTRY).subscribe((res) => {
      if (res.result.cvalidflag=="Y") {
        this.alert.showSuccess('Data Saved Succesfully', 'toast-bottom-right');
        this.createForm();
        this.templateDone=res.result.cerrmsg;
      }
      else
      {
        this.templateError=res.result.cerrmsg;
      }
    }, (err) => {
      this.alert.showError('Error occured try again !!', 'toast-bottom-right');
    })
  }
}
