import { Component } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms'
import { StrayModuleService } from 'src/app/shared/services/stray.module.service';
import { AppConstants } from 'src/app/app.constants';
import { NotificationAlertService,AESEncryptDecryptService } from 'bd-common-lib';
@Component({
  selector: 'app-mass-update',
  templateUrl: './mass-update.component.html',
  styleUrls: ['./mass-update.component.css']
})
export class MassUpdateComponent {


  massupdateForm!: FormGroup;
  empCode:string='';
  empLocArea:string='';

  constructor(
    private fb: FormBuilder,
    private strayModuleService: StrayModuleService,
    public alert: NotificationAlertService,
    private crypto : AESEncryptDecryptService) { }

  ngOnInit(): void {
    this.empCode=this.crypto.decryptionAES(sessionStorage.getItem('empCode'));
    this.empLocArea=this.crypto.decryptionAES(sessionStorage.getItem('empLocArea'));
    this.createForm();
  }

  createForm() {
    this.massupdateForm = this.fb.group({
      mcstraynos:['',[Validators.required]],
      mcstrayloc: ['', [Validators.maxLength(3)]],
      mcawbno: ['', [Validators.maxLength(11)]],
      mcstatus: ['O',Validators.required],
      mcremarks: ['', [Validators.maxLength(100)]],
    });
  }

  onSubmit() {
    let requestBody = this.massupdateForm.getRawValue();
    requestBody.mcTotal = null;
    requestBody.mcemplcode = this.empCode;
    requestBody.mcSubmit = '';

    this.strayModuleService.postDetails(requestBody, AppConstants.STRAY_MASS_UPDATE).subscribe((res: any) => {
      if (res.status.isSuccess) {
        this.alert.showSuccess('Data Saved Successfully', 'toast-bottom-right');
        this.massupdateForm.reset();
      }
    }, (err) => {
      console.log(err);
    })
  }
}
