import { Component } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms'
import { StrayModuleService } from 'src/app/shared/services/stray.module.service';
import { NotificationAlertService,AESEncryptDecryptService } from 'bd-common-lib';
import { AppConstants } from 'src/app/app.constants';
import * as moment from 'moment';
import { Title } from '@angular/platform-browser';

@Component({
  selector: 'app-mis-schedular',
  templateUrl: './mis-schedular.component.html',
  styleUrls: ['./mis-schedular.component.css']
})
export class MISSchedularComponent {
  schedularForm!: FormGroup;
  data: any = [];

  constructor(
    private fb: FormBuilder,
    private strayModuleService: StrayModuleService,
    public alert: NotificationAlertService,
    private crypto:AESEncryptDecryptService,
    private title:Title) { }

  ngOnInit(): void {
    this.title.setTitle("MIS Scheduler");
    this.createForm();
  }

  createForm() {
    this.schedularForm = this.fb.group({
      mcemplcode: [''],
      mreportid: ['000003'],
      mdatefrom: [moment(new Date()).format('YYYY-MM-DD'), Validators.required],
      mdateto: [moment(new Date()).format('YYYY-MM-DD'), Validators.required],
      mregion: [''],
      marea: [''],
      mcustomercode: [''],
      mgroupcode: [''],
      mtype: [''],
      mvalue: [''],
    });
  }

  onView() {
    //let requestBody = this.schedularForm.getRawValue();
    //requestBody.mSubmit = 'View';
    //requestBody.mdatefrom = moment(requestBody.mdatefrom).format('DD-MMM-YYYY');
    //requestBody.mdateto = moment(requestBody.mdateto).format('DD-MMM-YYYY');
    if (this.schedularForm.valid) {
      const payload = {
        "mreportid": this.schedularForm.value.mreportid,
        "mdatefrom": moment(this.schedularForm.value.mdatefrom).format('DD-MMM-YYYY'),
        "mdateto": moment(this.schedularForm.value.mdateto).format('DD-MMM-YYYY'),
        "mregion": this.schedularForm.value.mregion,
        "marea": "",
        "mcustomercode":"",
        "mgroupcode": "",
        "mtype": "",
        "mvalue": "",
        "msubmit":"DOWNLOAD"
      }
      console.log(payload)
      this.strayModuleService.postViewDetails(payload).subscribe((res: any) => {
        if (res.result && res.result.p_cursor) {
          this.data = res.result.p_cursor;
        } else {
          this.alert.showInfo('No Data Found', 'toast-bottom-right');
        }
      }, (err) => {
        console.log(err);
      })
    }
  }


  onSchdeule() {
    console.log(this.schedularForm.value.mreportid);
    if (this.schedularForm.value.mreportid != "" )
    {
    const payload = {
      "mcemplcode":this.crypto.decryptionAES(sessionStorage.getItem('empCode')),
      "mreportid": this.schedularForm.value.mreportid,
      "mdatefrom": moment(this.schedularForm.value.mdatefrom).format('DD-MMM-YYYY'),
      "mdateto": moment(this.schedularForm.value.mdateto).format('DD-MMM-YYYY'),
      "mregion": this.schedularForm.value.mregion,
      "marea": "",
      "mcustomercode":"",
      "mgroupcode": "",
      "mtype": "",
      "mvalue": "",
      "msubmit":"Schedule"
    }
    this.strayModuleService.postScheduleDetails(payload).subscribe((res: any) => {
      if (res.result) {
        this.createForm();
        this.alert.showSuccess('Report Scheduled Sucessfully', 'toast-bottom-right');
      }
    }, (err) => {
      console.log(err);
    })
  }
  else
  {
    this.alert.showError('Please enter Report Type', 'toast-bottom-right');
  }
  }
}
