import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { CsvService } from 'src/app/shared/services/csv.download.service';
import { StrayModuleService } from 'src/app/shared/services/stray.module.service';
import { NotificationAlertService } from 'bd-common-lib';
import * as moment from 'moment';
import { AppConstants } from 'src/app/app.constants';
import { Title } from '@angular/platform-browser';

@Component({
  selector: 'app-awb-listingus',
  templateUrl: './awb-listingus.component.html',
  styleUrls: ['./awb-listingus.component.css']
})
export class AwbListingusComponent implements OnInit {
  form!: FormGroup;
  templateError = '';
  mctypecheck = '';
  constructor(private fb: FormBuilder,
    private strayModuleService: StrayModuleService,
    public alert: NotificationAlertService,
    public csvService: CsvService,
    private title:Title) {
  }
  ngOnInit(): void {
    this.title.setTitle("AltInst AwbListing")
    this.createForm();
  }  
  createForm() {
    this.form = this.fb.group({
      groupcode: [''],
      fromdate: [moment(new Date()).format('YYYY-MM-DD'), Validators.required],
      todate: [moment(new Date()).format('YYYY-MM-DD'), Validators.required],
      statcode: [''],
      mctype: [0],
      mcawbno: [''],
    });
  }

  onSubmit() {
    this.mctypecheck="";
    this.templateError="";
    if(this.form.get('mctype')?.value == 0)
    {
      this.mctypecheck="RTO_COMPULSORY";
    }
    if(this.form.get('mctype')?.value == 1)
    {
      this.mctypecheck="COD_to_PREPAID";
    }
    if(this.form.get('mctype')?.value == 2)
    {
      this.mctypecheck="DBA";
    }
    let requestBody = this.form.getRawValue();
    requestBody.fromdate = moment(requestBody.fromdate).format('DD-MMM-YYYY');
    requestBody.todate = moment(requestBody.todate).format('DD-MMM-YYYY');
    if ((this.form.get('groupcode')?.value == null || this.form.get('groupcode')?.value == "") && this.form.get('mctype')?.value == 2) {
      this.strayModuleService.postDetails(requestBody, AppConstants.AWB_LISTING).subscribe((res: any) => {
        if (res.result) {
          let data: any = [];
          if (res.result.c1) {
            data = [...data, ...res.result.c1];
          }
          if (res.result.c2) {
            data = [...data, ...res.result.c2];
          }
          if (res.result.c3) {
            data = [...data, ...res.result.c3];
          }
          if (data.length) {            
            this.csvService.exportToCsv(this.mctypecheck + '.csv', data);
            this.alert.showSuccess('CSV Downloaded Successfully', 'toast-bottom-right');
          } else {
            this.alert.showInfo('No Data Found', 'toast-bottom-right');
          }
        }
      }, (err) => {
        console.log(err);
      })
    }
    else {
      if (this.form.get('groupcode')?.value == null || this.form.get('groupcode')?.value == "") {
        this.templateError = "Please enter Group Code"
      }
      else {
        this.strayModuleService.postDetails(requestBody, AppConstants.AWB_LISTING).subscribe((res: any) => {
          if (res.result) {
            let data: any = [];
            if (res.result.c1) {
              data = [...data, ...res.result.c1];
            }
            if (res.result.c2) {
              data = [...data, ...res.result.c2];
            }
            if (res.result.c3) {
              data = [...data, ...res.result.c3];
            }
            if (data.length) {
              this.alert.showSuccess('CSV Downloaded Successfully', 'toast-bottom-right');
              this.csvService.exportToCsv('StrayModuleDetails.csv', res.result);
            } else {
              this.alert.showInfo('No Data Found', 'toast-bottom-right');
            }
          }
        }, (err) => {
          console.log(err);
        })
      }
    }
  }
}

