import { Component,Injectable, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { StrayModuleService } from 'src/app/shared/services/stray.module.service';
import { NotificationAlertService, AESEncryptDecryptService } from 'bd-common-lib';
import { AppConstants } from 'src/app/app.constants';
import { NgbCalendar, NgbDateAdapter, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import * as moment from 'moment';

@Component({
  selector: 'app-stray-update',
  templateUrl: './stray-update.component.html',
  styleUrls: ['./stray-update.component.css']
})

@Injectable()
export class StrayUpdateComponent implements OnInit {

  editStrayNo:string='';
  strayEntryForm!: FormGroup;
  readonly months = [undefined, 'JAN', 'FEB', 'MAR', 'APR', 'MAY', 'JUN', 'JUL', 'AUG', 'SEP', 'OCT', 'NOV', 'DEC'];
  empCode:string='';
  empLocArea:string='';

  constructor(
    private fb: FormBuilder,
    private strayModuleService: StrayModuleService,
    public alert: NotificationAlertService,
    private ngbCalendar: NgbCalendar,
    private dateAdapter: NgbDateAdapter<string>,
    private crypto:AESEncryptDecryptService
    ) { }

  ngOnInit(): void {
    this.empCode=this.crypto.decryptionAES(sessionStorage.getItem('empCode'));
    this.empLocArea=this.crypto.decryptionAES(sessionStorage.getItem('empLocArea'));
    this.createForm();
    
  }

  createForm() {
    this.strayEntryForm = this.fb.group({      
      mdstraydate: [[Validators.required]],
      mcstrayloc: [this.empLocArea, [Validators.required]],
      mcpackdesc: ['', [Validators.required]],
      mcvehicleno: [''],      
      mcroute: [''],
      mcawbno:[''],
      mcstatus:[''],
      mcremarks: ['']
    });
  }

  onSubmit() {

    //alert(this.strayEntryForm.controls['mdstraydate'].value);

    let strayDTMonth = this.months[Number(this.strayEntryForm.controls['mdstraydate'].value!.trim().split("-")[1])];
    let strayDTDate = this.strayEntryForm.controls['mdstraydate'].value!.split("-")[0] + '-' + strayDTMonth + '-' + this.strayEntryForm.controls['mdstraydate'].value!.split("-")[2];

    let requestBody : any = {
      ...this.strayEntryForm.value,
      mdstraydate : strayDTDate,
      mcstrayno : this.editStrayNo,
      mcSubmit : 'mcSubmit',
      mcEmplCode : this.empCode
    } 
    
    console.log(requestBody);

    this.strayModuleService.postDetails(requestBody, AppConstants.STRAY_ENTRY).subscribe((res: any) => {
      console.log(res);

      if (res.result) {
        if(res.result.cvalidFlag === 'Y')
        {
          this.alert.showSuccess('Data Saved Successfully', 'toast-bottom-right');
          
          this.strayEntryForm.reset();
          this.strayEntryForm.patchValue({
            mdstraydate: [moment(new Date()).format('DD-MM-YYYY')],
            mcstrayloc: [this.empLocArea, [Validators.required]],
          });
        }
        else{
          this.alert.showError(res.result.errormsg, 'toast-bottom-right');
        }
      }
    }, (err) => {
      this.alert.showError('Error occured try again!!', 'toast-bottom-right');
    })
  }

  setEditForm(data:any)
  {
    this.editStrayNo = data.cstrayno;
    this.strayEntryForm.patchValue({
      mcstrayloc : data.cstrayloc
    })
  }
}
