import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { AppConstants } from 'src/app/app.constants';

@Injectable({
  providedIn: 'root',
})
export class StrayModuleService {
  constructor(private httpClient: HttpClient) { }

  postDetails(payload: any, endPoint: string): Observable<any> {
    return this.httpClient.post(endPoint, payload);
  }  

  postScheduleDetails(payload: any): Observable<any> {
    return this.httpClient.post(AppConstants.SCHEDULER, payload);
  }

  postViewDetails(payload: any): Observable<any> {
    return this.httpClient.post(AppConstants.SCHEDULER, payload);
  }
//Testing
}
