import { NgModule,APP_INITIALIZER } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { BDCommonLibModule } from 'bd-common-lib';
import { HomeComponent } from './components/home/home.component';
import { CommonModule } from '@angular/common';
import { HTTP_INTERCEPTORS, HttpClientModule } from '@angular/common/http';
import { NavComponent } from './components/nav/nav.component';
import { AlternateCustomerMasterComponent } from './components/alternate-customer-master/alternate-customer-master.component';
import { AwbListingusComponent } from './components/awb-listingus/awb-listingus.component';
import { CAPXModuleComponent } from './components/capx-module/capx-module.component';
import { MISSchedularComponent } from './components/mis-schedular/mis-schedular.component';
import { StrayModuleComponent } from './components/stray-module/stray-module.component';
import { NotificationComponent } from './components/notification/notification.component';
import { StrayEntryComponent } from './components/stray-entry/stray-entry.component';
import { QueryComponent } from './components/query/query.component';
import { MassUpdateComponent } from './components/mass-update/mass-update.component';
import { DownloadCSVComponent } from './components/download-csv/download-csv.component';
import { ReactiveFormsModule } from '@angular/forms';
import { PackStayComponent } from './pack-stay/pack-stay.component';
import { NotificationentryformComponent } from './components/notificationentryform/notificationentryform.component';
import { CustomerMasterComponent } from './components/customer-master/customer-master.component';
import { CustomerGroupEnrtyComponent } from './components/customer-group-enrty/customer-group-enrty.component';
import { NgbModule, NgbAlertModule, NgbDatepickerModule, NgbDateParserFormatter, NgbDateAdapter } from '@ng-bootstrap/ng-bootstrap';
import { CustomDateParserFormatter, CustomAdapter } from './shared/services/datepicker-adapter';
import { LoaderComponent } from './shared/components/loader/loader.component';
import { NgxLoadingModule } from 'ngx-loading';
import { APIInterceptor } from './shared/http-interceptor';
import { StrayUpdateComponent } from './components/stray-update/stray-update.component';
import { UnAuthorizedAccessComponent } from './shared/components/un-authorized-access/un-authorized-access.component';

@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    NavComponent,
    AlternateCustomerMasterComponent,
    AwbListingusComponent,
    CAPXModuleComponent,
    MISSchedularComponent,
    StrayModuleComponent,
    NotificationComponent,
    StrayEntryComponent,
    QueryComponent,
    MassUpdateComponent,
    DownloadCSVComponent,
    PackStayComponent,
    NotificationentryformComponent,
    CustomerMasterComponent,
    CustomerGroupEnrtyComponent,
    LoaderComponent,
    StrayUpdateComponent,
    UnAuthorizedAccessComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    BDCommonLibModule,
    CommonModule,
    HttpClientModule,
    ReactiveFormsModule,
    NgbModule,
    NgbDatepickerModule,
    NgxLoadingModule.forRoot({}),
  ],
  providers: [
    { provide: NgbDateAdapter, useClass: CustomAdapter },
    { provide: NgbDateParserFormatter, useClass: CustomDateParserFormatter },
    {
      provide: HTTP_INTERCEPTORS,
      useClass: APIInterceptor,
      multi: true
    }
  ],
  entryComponents: [LoaderComponent],
  bootstrap: [AppComponent]
})
export class AppModule { }
