import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, tap } from 'rxjs';
import { AppConstants } from 'src/app/app.constants';

@Injectable({
  providedIn: 'root'
})
export class CapxService {

  constructor(private httpClient: HttpClient) { }

  postDetails(payload: any, endPoint: string): Observable<any> {
    return this.httpClient.post(endPoint, payload);
  }

  public GetCapaxDetails(body: any): Observable<any> {
    return this.httpClient.post<any>(AppConstants.CAPEX + '/paraForm', { params: body });
  }

  public GetDrpDetails(body: any): Observable<any> {
    return this.httpClient.post<any>(AppConstants.CAPEX + '/getparaformreg', { params: body });
  }

  public GetRegDetails(body: any): Observable<any> {
    return this.httpClient.get<any>(AppConstants.CAPEX + '/getparaformreg', { });
  }

  public GetLocDetails1(body: any): Observable<any> {
    return this.httpClient.get<any>(AppConstants.CAPEX + '/getparaformloc', { });
  }

  GetLocDetails(region:string): Observable<any[]> {
   return this.httpClient.get<any[]>(AppConstants.CAPEX  + '/getparaformloc?pcloccd='+region).pipe(
       tap()
   );
 }

  public GetAreaDetails1(body: any): Observable<any> {
    return this.httpClient.get<any>(AppConstants.CAPEX + '/getparaformarea', { });
  }

  GetAreaDetails(region:string): Observable<any[]> {
    return this.httpClient.get<any[]>(AppConstants.CAPEX  + '/getparaformarea?pcloccd='+region).pipe(
        tap()
    );
  }

}
